import React from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import { Box } from '@mui/material';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import Header from './components/Layout/Header';
import Sidebar from './components/Layout/Sidebar';
import LoginForm from './components/Auth/LoginForm';
import RegisterForm from './components/Auth/RegisterForm';
import Dashboard from './components/Dashboard/Dashboard';
import InternshipList from './components/Internships/InternshipList';
import PlaceholderComponent from './components/Placeholder/PlaceholderComponent';

const ProtectedRoute: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const { user, isLoading } = useAuth();

  if (isLoading) {
    return <div>Loading...</div>;
  }

  if (!user) {
    return <Navigate to="/login" replace />;
  }

  return <>{children}</>;
};

const Layout: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  return (
    <Box sx={{ display: 'flex' }}>
      <Header />
      <Sidebar />
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          mt: 8,
          ml: '240px',
          minHeight: '100vh',
          bgcolor: 'grey.50'
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

const AppRoutes: React.FC = () => {
  return (
    <Routes>
      <Route path="/login" element={<LoginForm />} />
      <Route path="/register" element={<RegisterForm />} />
      <Route
        path="/dashboard"
        element={
          <ProtectedRoute>
            <Layout>
              <Dashboard />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/internships"
        element={
          <ProtectedRoute>
            <Layout>
              <InternshipList />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/companies"
        element={
          <ProtectedRoute>
            <Layout>
              <PlaceholderComponent 
                title="Companies Management" 
                description="Manage companies and their information. This feature will be available soon."
              />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/students"
        element={
          <ProtectedRoute>
            <Layout>
              <PlaceholderComponent 
                title="Students Management" 
                description="View and manage student information. This feature will be available soon."
              />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/attendance"
        element={
          <ProtectedRoute>
            <Layout>
              <PlaceholderComponent 
                title="Attendance Tracking" 
                description="Track student attendance during internships. This feature will be available soon."
              />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/reports/weekly"
        element={
          <ProtectedRoute>
            <Layout>
              <PlaceholderComponent 
                title="Weekly Reports" 
                description="Submit and view weekly internship reports. This feature will be available soon."
              />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/reports/final"
        element={
          <ProtectedRoute>
            <Layout>
              <PlaceholderComponent 
                title="Final Reports" 
                description="Submit and view final internship reports. This feature will be available soon."
              />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/certificates"
        element={
          <ProtectedRoute>
            <Layout>
              <PlaceholderComponent 
                title="Certificates" 
                description="Generate and view internship certificates. This feature will be available soon."
              />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/users"
        element={
          <ProtectedRoute>
            <Layout>
              <PlaceholderComponent 
                title="User Management" 
                description="Manage system users and their roles. This feature will be available soon."
              />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/settings"
        element={
          <ProtectedRoute>
            <Layout>
              <PlaceholderComponent 
                title="Settings" 
                description="Configure system settings and preferences. This feature will be available soon."
              />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route
        path="/profile"
        element={
          <ProtectedRoute>
            <Layout>
              <PlaceholderComponent 
                title="User Profile" 
                description="View and edit your profile information. This feature will be available soon."
              />
            </Layout>
          </ProtectedRoute>
        }
      />
      <Route path="/" element={<Navigate to="/dashboard" replace />} />
    </Routes>
  );
};

const App: React.FC = () => {
  return (
    <AuthProvider>
      <AppRoutes />
    </AuthProvider>
  );
};

export default App;
